import { NzModalService } from 'ng-zorro-antd/modal';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { VhAlgorithm, VhQuery, VhQuerySales } from 'ionic-vhframeworks';
import { LanguageService } from 'src/app/sales/services/language.service';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';

@Component({
  selector: 'app-design',
  templateUrl: './design.component.html',
  styleUrls: ['./design.component.scss']
})
export class DesignComponent implements OnInit {

  list_barcode: any = [];
  showModalAdd = false;
  showModalEdit = false;
  id_barcode: any = '';
  formAdd: FormGroup;
  barcode_name = ''
  constructor(
    private router: Router,
    public languageService: LanguageService,
    private nzModalService: NzModalService,
    private vhQuery: VhQuery,
    private vhComponent: VhComponent,
    private vhQuerySales: VhQuerySales,
    private vhAlgorithm: VhAlgorithm
  ) { }

  ngOnInit(): void {
    this.getData();
    this.initForm();
  }

  /**
   * get dữ liệu mẫu thiết kế type 2 cho thẻ trả trước
   */
  getData() {
    this.vhComponent.showLoading('').then(() => {
      this.vhQuery.getDocsByFields("barcode_designs", { id_branch: { $eq: this.vhQuerySales.getDefaultBranch()._id }, type: {$eq: 2} })
        .then((response: any) => {
          this.list_barcode = response.map(item => { item.property = this.vhAlgorithm.sortNumberbyASC(item.property, 'index'); return { ...item } })
          this.vhComponent.hideLoading(0);
        })
    })
  }

  /**
   * khởi tạo form lưu các trường của tem để lưu DB
   */
  initForm() {
    this.formAdd = new FormGroup({
      name: new FormControl('', Validators.required),
      type: new FormControl(2),
      img: new FormControl('assets/img/sample_coupon.jpg'),
      display_img: new FormControl(true),
      width: new FormControl(150, Validators.required),
      height: new FormControl(70, Validators.required)
    })
  }
  /**
   * tạo mẫu thiết kế lên DB
   */
  addBarcode() {
    let w = this.formAdd.value.width
    let h = this.formAdd.value.height
    if (h > w) { this.formAdd.controls['width'].setValue(h); this.formAdd.controls['height'].setValue(w); }
    this.vhComponent.showLoading('').then(() => {
      this.vhQuery.addDoc("barcode_designs", {
        ...this.formAdd.value, id_branch: this.vhQuerySales.getDefaultBranch()._id, property: [
          {
          type: 'date_validity',
          font_size: 12,
          text_align: 'end',
          status: true,
          value: '10/02/2020',
          font_weight: 'bold', // bold | 500
          font_style: 'normal', // normal | italic
          text_decoration: 'none', // underline | none
          index: 0,
          y: (this.formAdd.value.height) *0.3 * 3.78,
          x:  (this.formAdd.value.width) * 0.6* 3.78 ,
          height: (this.formAdd.value.height) * 0.1,
          width: (this.formAdd.value.width) * 0.4,
        },{
          type: 'date_expire',
          font_size: 12,
          text_align: 'end',
          status: true,
          value: '10/02/2020',
          font_weight: 'bold', // bold | 500
          font_style: 'normal', // normal | italic
          text_decoration: 'none', // underline | none
          index: 1,
          y: (this.formAdd.value.height) *0.4 * 3.78,
          x:  (this.formAdd.value.width) * 0.6* 3.78 ,
          height: (this.formAdd.value.height) * 0.1,
          width: (this.formAdd.value.width) * 0.4,
        },
        {
          type: 'barcode',
          font_size: 12,
          text_align: 'end',
          status: true,
          value: "123456789",
          font_weight: 'bold', // bold | 500
          font_style: 'normal', // normal | italic
          text_decoration: 'none', // underline | none
          white_space: 'nowrap', // nowrap| normal
          y: (this.formAdd.value.height) * 0.5 * 3.78,
          x: this.formAdd.value.width*0.6* 3.78,
          height: (this.formAdd.value.width) * 0.1,
          index: 2,
          width: this.formAdd.value.width*0.4,
          justify_content: 'end',
        },
        {
          type: 'qr',
          font_size: 12,
          text_align: 'end',
          status: false,
          value: "123456789",
          font_weight: 'bold', // bold | 500
          font_style: 'normal', // normal | italic
          text_decoration: 'none', // underline | none
          white_space: 'nowrap', // nowrap| normal
          y: (this.formAdd.value.height) * 0.5 * 3.78,
          x: this.formAdd.value.width*0.6* 3.78,
          height: (this.formAdd.value.width) * 0.2,
          index: 3,
          width: this.formAdd.value.width*0.2,
          justify_content: 'end',
        },
        {
          type: 'barcode-text',
          font_size: 12,
          text_align: 'center',
          status: true,
          value: "123456789",
          font_weight: '500', // bold | 500
          font_style: 'normal', // normal | italic
          text_decoration: 'none', // underline | none
          white_space: 'nowrap', // nowrap| normal
          y: (this.formAdd.value.height) * 0.7 * 3.78,
          x: this.formAdd.value.width*0.6* 3.78,
          height: (this.formAdd.value.height) * 0.1,
          index: 4,
          width: this.formAdd.value.width*0.4,
          justify_content: 'end',
        },
      ]
      })
        .then((response) => {
          this.vhComponent.hideLoading(0);
          this.list_barcode.push(response);
          this.showModalAdd = false;
        }, error => {
          this.vhComponent.hideLoading(0);
          this.vhComponent.alertMessageDesktop("error", this.languageService.translate("An error while adding price list"))
        })
    })

  }

  /**
   * cập nhật tên mẫu thiết kế lên DB
   */
  editBarcode() {
    if (this.barcode_name != '')
      this.vhComponent.showLoading('').then(() => {
        this.vhQuery.updateDoc("barcode_designs", this.id_barcode, { name: this.barcode_name }).then((bool) => {
          this.vhComponent.hideLoading(0);
          this.showModalEdit = false;
          this.list_barcode[this.list_barcode.findIndex(item => item._id == this.id_barcode)].name = this.barcode_name;
        }, error => {
          this.vhComponent.hideLoading(0);
          
        })
      })
  }

  gotoEdit(item) {
    this.router.navigate(['/sales/dashboard/manage/sales-program/coupons/design/detail'], { state: item });
  }

  /**
   * xóa mẫu thiết kế khỏi DB
   */
  deleteBarcode(item, index) {
    this.nzModalService.confirm({
      nzTitle: this.languageService.translate("Are you sure to delete this label template?"),
      nzCancelText: this.languageService.translate("Cancel"),
      nzOnOk: () => {
        this.vhComponent.showLoading('').then(() => {
          if (this.vhQuerySales.getlocalProducts().filter(i => i.id_design_barcode == item._id).length) {
            this.nzModalService.error({
              nzTitle: this.languageService.translate("Mẫu tem này đang liên kết với coupon, không thể xóa!"),
              nzCancelText: this.languageService.translate("Cancel"),
            })
            this.vhComponent.hideLoading(0);
          }
          else
            this.vhQuery.deleteDoc("barcode_designs", item._id)
              .then((bool) => {
                this.list_barcode.splice(index, 1)
                this.vhComponent.hideLoading(0);
              })
        })
      },
    })
  }


  goBack() {
    this.router.navigate(['/sales/dashboard/manage/sales-program/coupons'])
  }

}
